.start-reset-button {
  width: 30%;
  height: 30px;
  align-self: center;
}

.win-message {
  color: white;
  align-self: center;
}

.damage-display-total__container {
  width: 100%;
}

.join-game-button {
  width: 50%;
  height: 75px;
  align-self: center;
}

.new-game-separator {
  height: 2px;
  width: 200%;
  border-bottom: 1px solid grey;
}

.dice-roller-container {
    display: flex;
    flex-direction: column;
    height: 50%;
}

.dice-roller__dice-row {
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.selected {
    border: 1px solid white;
}

.dice-roller__roll-button {
    width: 50%;
    height: 30px;
    align-self: center;
}

.dice-roller__enemy-text {
    align-self: center;
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 18pt;
}

.dice-roller__die-player {
    position: relative;
    top: 85px;
}

.dice-roller__lane {
    height: 100%;
}

.dice-roller__die-enemy {
    position: relative;
    opacity: 0;
    top: 0;
}

.animate-player-ready {
    transition-property: top;
    transition-duration: 0.25s;
    top: 0;
}

.animate-enemy-ready {
    transition-property: top, opacity;
    transition-duration: 0.25s, 0.25s;
    top: 110px;
    opacity: 1;
}

.die-container__multiplier {
    color: white;
    text-align: center;
    height: 21px;
}

.die-component__die-container {
    width: 60px;
    height: 60px;
    padding: 1px;
    padding-left: 3px;
    padding-top: 3px;
}

.attack {
    background: rgba(237, 64, 102, 0.82);
}

.defend {
    background: rgba(51, 71, 249, 0.82);
}

.game-board {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 80%;
}
.damage-display-lane__container {
    width: 100%;
    height: 150px;
    border: solid red 1px;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.damage-display__icon {
    height: 50%;
    width: 10%;
}

.damage-display-lane__score-container {
    height: 50%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid red;
}

.damage-display-lane__attack-container {
    height: 100%;
    width: 80%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-left: 20px;
    margin-right: 20px;
}

.damage-display-lane__defense-container {
    height: 100%;
    width: 80%;
    display: flex;
    align-items: center;
    margin-left: 20px;
    margin-right: 20px;
}
.player {
    color: white;
    height: 10%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.hp-bar-container {
    width: 40%;
    height: 35%;
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
}

.hp-bar-fill {
    background-color: firebrick;
    height: 100%;
}

.hp-bar-text {
    position: absolute;
    margin: 0;
}
.damage-display-total__container {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.damage-display-total__damage-text {
    color: red;
}
